/* Start - Green Sock */
.green-sock {
  max-height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  .grid-row {
    scroll-snap-align: start;
    height: 100vh;

    .grid {
      div {
        align-self: center;
        justify-self: center;
        text-align: center;
      }
    }
  }
}
/* End - Green Sock */
